import { Component, Host, Prop, h } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { ButtonVariant } from '../../atoms/button/button.types';
import { BsButton } from '../../atoms/button/button';
import { TextSize } from '../../atoms/text/text.types';
import { BsTitle } from '../../atoms/title/title';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';

/**
 * CTA Banner component.
 */

@Component({
  tag: 'bs-cta-banner',
  styleUrl: 'cta-banner.scss',
  shadow: true,
})
export class BsCTABanner {
  /**
   * The (json)object containing the bs-title element properties
   */
  @Prop() bannerTitle: BsTitle | string;

  @Memoize('bannerTitle')
  get parsedBannerTitle(): BsTitle {
    return parseIfString(this.bannerTitle);
  }

  /**
   * The banner subtitle
   */
  @Prop() subtitle?: string;

  /**
   * The (json)object containing the bs-button element properties.
   */
  @Prop() button: BsButton | string;

  @Memoize('button')
  get parsedButton(): BsButton {
    return parseIfString(this.button);
  }

  /** The banner variant. */
  @Prop() variant?: ButtonVariant = ButtonVariant.Primary;

  /** Should the background be filled up? */
  @Prop() filled?: boolean = false;

  render() {
    return (
      <Host class={`is-${this.variant}-variant ${this.filled ? 'is-filled' : ''}`}>
        <div class="cta-banner">
          <div class="text-wrapper">
            <bs-title class="title" {...this.parsedBannerTitle} />
            {this.subtitle && <bs-text class="sub" text={this.subtitle} size={TextSize.Default} />}
          </div>

          <bs-button
            {...this.parsedButton}
            isFullWidth={
              this.variant === ButtonVariant.Secondary || compareBreakpoint('max', Breakpoint.md)
            }
            variant={this.variant}
            ghost={true}
          />
        </div>
      </Host>
    );
  }
}
