:host .cta-banner {
  display: flex;
  flex-direction: column;
  gap: rem(24);
  justify-content: space-between;
  padding: rem(24) rem(16);
  border: 1px solid $gray-800;
  border-radius: rem(16);

  @include mq('min', 'md') {
    flex-direction: row;
    align-items: center;
    padding: rem(40);
  }
}

:host(.is-filled) .cta-banner {
  --button-bg-color: $light-gray;
  background-color: $light-gray;
  border: 0;
}

:host .sub {
  color: $gray-700;
}

:host(.is-primary-variant) .text-wrapper {
  flex: 1 0 50%;
  flex-wrap: wrap;
  text-align: center;

  bs-title {
    display: block;
    margin-bottom: rem(8);
  }

  @include mq('min', 'md') {
    text-align: left;
  }
}

:host(.is-secondary-variant) {
  .cta-banner {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: rem(8);
    border-radius: rem(8);
  }

  .text-wrapper {
    display: flex;
    flex-direction: column;
    text-align: center;

    .title {
      width: 100%;
    }

    @include mq('min', 'md') {
      padding: rem(25) rem(30) 0;
    }
  }
}
